* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  background-color: #999;
  color: #284d5f;
}

.wrapper {
  max-width: 1200px;
  background: rgba(255, 255, 255, 0.5);
  margin: 0 auto;
  box-shadow: 0 0 18px rgba(0, 0, 0, 0.35);
  margin-top: 18px;
}

header {
  height: 120px;
  border-bottom: 1px solid rgba(40, 77, 95, 0.8);
}

nav.primary {
  width: 100%;
}

nav.primary li {
  list-style: none;
  float: left;
}

nav.primary li a {
  display: block;
  padding: .5em;
  text-decoration: none;
}

.main {
  float: none;
  clear: both;
}

.pad {
  padding: 1rem;
}

footer {
  text-align: center;
}
